.container {
  height: 100%;
  display: flex;
  justify-content: center;
  color: white;
  position: relative;
}
.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 54px;
  color: white;
}
.movie__title {
  font-size: 44px;
  font-weight: 600;
}
.box {
  position: relative;
  width: 1440px;
  margin: 0px auto;
  display: flex;
  justify-content: space-between;
}
.img {
  margin-top: 50px;
  width: 400px;
  object-fit: cover; /* 이미지 비율을 유지하면서 지정된 크기에 맞게 자릅니다 */
  image-rendering: auto; /* 이미지의 렌더링 품질을 개선합니다 (기본값은 auto) */
}
.text {
  width: 55%;
  margin-top: 50px;
  margin-left: 300px;
  font-size: 28px;
}
.bg {
  width: 100vw;
  height: calc(-60px + 100vh);
  padding-top: 50px;
  color: rgb(255, 255, 255);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}
