.header {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  padding: 0px 20px;
  border-bottom: 1px solid rgb(0, 0, 0);
  background-color: rgb(0, 0, 0);
}

.logo {
  font-size: 2em;
  font-weight: bold;
  color: #e50914;
  text-decoration: none;
}
