.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 54px;
  color: white;
}

.font {
  font-size: 56px;
  font-weight: 600;
}
.link {
  text-decoration: none;
  color: rgb(229, 9, 20);
}

.linkHover:hover {
  animation: scale 2s ease-in-out;
}

@keyframes scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(2);
  }
  100% {
    transform: scale(1);
  }
}
.bg {
  width: 100vw;
  height: calc(-60px + 100vh);
  padding-top: 50px;
  color: rgb(255, 255, 255);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}
